// vmvuex
module.exports = (vm) => {
	// 
	uni.$u.http.setConfig((config) => {
		/* config */
		// config.baseURL = "http://webapibeta.mzsale.com/"; /*  */
		config.baseURL ='https://rcygweb.mzsale.cn/'; //http://211.149.188.114:92/'// "http://self.mzsale.com/"
		if(uni.getStorageSync('cxbb')==true){
			console.log('')
		}
		return config
	})
	// 
	uni.$u.http.interceptors.request.use((config) => { // async await 
		// dataundefined{}
		config.data = config.data || {}
		// customtoken
		// if(config?.custom?.auth) {
		//vmvuexvm.$store.state
		// 	config.header.token = vm.$store.state.userInfo.token
		// }
		return config
	}, config => { // async await 
		return Promise.reject(config)
	})

	// 
	uni.$u.http.interceptors.response.use((response) => {
		/*  async await */
		const data = response.data
		//console.log('',data)
		
			if(data.error_code=='40002'){
				uni.request({
					url: 'https://rcygweb.mzsale.cn/mzsale/web/token',
					data: {
						userid:`a${uni.getStorageSync('userid')}`,
						refresh_token:uni.getStorageSync('refresh_token')
					},
					method:'POST',
					success: (res) => {
						if(res.data.error_code=='40004'|| res.data.error_code=='40005'){
							uni.redirectTo({
								url:'../../pages/login/login'
							})
						}else {
							uni.setStorageSync('access_token',res.data.access_token)
						}
					}
				});
			}
			
		
		//
		// const custom = response.config?.custom
		// if (data.err_code !== 0) {
		// 	// customtoastfalsetoast
		// 	if (custom.toast !== false) {
		// 		uni.$u.toast(data.message)
		// 	}
		// 	// catchreject
		// 	if (custom?.catch) {
		// 		return Promise.reject(data)
		// 	} else {
		// 		// pendingpromisecatch
		// 		return new Promise(() => {})
		// 	}
		// }
		return data === undefined ? {} : data
	}, (response) => {
		//  statusCode !== 200
		return Promise.reject(response)
	})
}
